import React, { useEffect, useState } from "react";
import { Row, Typography, Popconfirm, message, Col, Select, Card, Button } from "antd";
import { useHistory } from "react-router-dom";

/** Icons */
import {
  DeleteOutlined,
  EditOutlined,
  UploadOutlined
} from "@ant-design/icons";
import Note from "@2fd/ant-design-icons/lib/Note";
import Archive from "@2fd/ant-design-icons/lib/Archive";

/** notes service */
import noteService from "./../../services/note"

/** Categories Service */
import categoriesService from "./../../services/category";

import "./notes.less";

const { Title } = Typography;

const { Option } = Select;

const cancel = (e) => {
  message.error('Click on No');
};

const Notes = () => {
  
  const history = useHistory();

  const [activeNote, setActiveNote] = useState(true);
  const [location, setLocation] = useState(null);
  const [categories, setCategories] = useState([]);
  const [categFilter, setCategFilter] = useState(null);
  const [allData, setallData] = useState([]);
  const [data, setData] = useState([]);

  useEffect(() => {
    setLocation(window.location.pathname);
    getAllCategories();
    if(window.location.pathname === "/activeNotes"){
        setActiveNote(true);
        getActiveNotes();
    }else{
        setActiveNote(false);
        getArchivedNotes();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [!location, window.location.pathname])   
  
  /** Get All Categories */
  async function getAllCategories (){
    try {
        let resp = await categoriesService.getAll();
        if (resp.status){
            let aux = [...resp.data, { id: resp.data.length + 1, name: "ALL", status: true, createdAt: "-", updatedAt: "-" }];
            setCategories(aux);
        }    
    } catch (error) {
        message.error("An error has ocurred. Please try again");
    }        
  }

  /** Filter notes by category */
//   const getNotesByCategory = () => {
//     let newNotes;
//     categFilter !== categories.length  ?
//         newNotes = allData.filter((item) => item.categories === categFilter)
//     : 
//         newNotes = allData
    
//   }

  /** Get Notes  */
  async function getActiveNotes(){
    try {
        let resp = await noteService.getActive();
        if(resp.status){
            let aux = resp.data;
            setData(aux?.data[0]?.Notes);
            setallData(aux?.data[0]?.Notes);
        }        
    } catch (error) {
        message.error("An error has ocurred. Please Sign in again ");
    }
  }

  async function getArchivedNotes(){
    try {
        let resp = await noteService.getArchived();
        if(resp.status){
            let aux = resp.data;
            setData(aux[0]?.Notes);
            setallData(aux?.data[0]?.Notes);
        }        
    } catch (error) {
        message.error("An error has ocurred. Please Sign in again ");
        
    }
  }



  async function archiveNote(id) {
      
      try {
        let resp = await noteService.archive(id);
        if(resp.status){
            getActiveNotes();
            message.success('Note successfully archived');
        }
      } catch (error) {
        
            message.error("An error has ocurred. Please try again ");
        
      }
  }

  async function activateNote(id) {
      
    try {
    const token = localStorage.getItem("token");
      let resp = await noteService.activate(id, token);
      if(resp.status){
          getArchivedNotes();
          message.success('Note successfully activated');
      }
    } catch (error) {
          message.error("An error has ocurred. Please try again ");
      
    }
  }

  async function deleteNote(id){
    try {
      let resp = await noteService.delete(id);
      if(resp.status){
          activeNote ? getActiveNotes() : getArchivedNotes()
          message.success('Note deleted');
      }
    } catch (error) {
          message.error("An error has ocurred. Please try again ");      
    }
  }
  
return (
  <>

    <Card className="card-dashboard">
        {
            activeNote ?   
                <Row>
                    <h1>Active Notes</h1>                     
                    <Button type="primary" size="medium" onClick={() => { history.push("/edit"); }} style={{ marginLeft: "20px"}}>
                        Create Note
                    </Button>
                </Row>                
            
            : 
                <h1>Archived Notes</h1>
        }   
        
        <Row
            gutter={[8, { xs: 8, sm: 16, md: 16, lg: 16, xl: 16 }]}
            style={{ padding: "5px 10px" }}
        >        
            <Col
            xs={{ span: 24, offset: 0 }}
            sm={{ span: 24, offset: 0 }}
            md={{ span: 24, offset: 0 }}
            lg={{ span: 8, offset: 0 }}
            xl={{ span: 8, offset: 0 }}
            xxl={{ span: 8, offset: 0 }}
            style={{
                textAlign: "left",
                alignItems: "center",
                display: "flex",
            }}
            >
            <Select
                style={{ width: "100%" }}
                size="medium"
                placeholder="Categories"
                onChange={(key) => setCategFilter(key)}
                >
                {categories.map((item) => {
                    return <Option key={item.id} value={item.value}>{item.name}</Option>;
                })}
            </Select>
            </Col>
            <Col
            xs={{ span: 24, offset: 0 }}
            sm={{ span: 24, offset: 0 }}
            md={{ span: 24, offset: 0 }}
            lg={{ span: 3, offset: 0 }}
            xl={{ span: 3, offset: 0 }}
            xxl={{ span: 3, offset: 0 }}
            style={{
                textAlign: "left",
                alignItems: "center",
                display: "flex",
            }}
            >
            <Button type="primary" size="medium" style={{ width: "100%" }} 
                //onClick={getNotesByCategory} 
            >
                Filter
            </Button>           
            </Col>
        </Row>
            
    </Card>
        {
            data && data?.length > 0 ? 
                <Row gutter={[16, { xs: 8, sm: 16, md: 8, lg: 8, xl: 8 }]}>
                    {
                        data.map((item, i) => (
                            <Col
                                key={item.key + "-"}
                                xs={{ span: 24, offset: 0 }}
                                sm={{ span: 24, offset: 0 }}
                                md={{ span: 24, offset: 0 }}
                                lg={{ span: 12, offset: 0 }}
                                xl={{ span: 12, offset: 0 }}
                            >
                            <Card className="card-dashboard" >
                                <Row>
                                    <Col className="column-icon">
                                        <Note className="icon"/>
                                    </Col>
                                    <Col>
                                        <Title level={5} className="title">
                                            {item.title}
                                        </Title>
                                        <p>{item.content}</p>
                                    </Col>
                                </Row>                        
                                <div>
                                    {
                                        activeNote ?                                
                                            <Typography.Link 
                                                onClick={()=>archiveNote(item.id)}                                        
                                            >
                                                <Archive className="action-icon"/>
                                            </Typography.Link> 
                                        :
                                            <Typography.Link    
                                                onClick={() => {
                                                    activateNote(item.id)
                                                }}                                        
                                            >
                                                <UploadOutlined className="action-icon"/>                                        
                                            </Typography.Link> 
                                    }
                                    <Typography.Link 
                                        onClick={() => history.push("/edit/" + item.id)}
                                        
                                    >
                                        <EditOutlined className="action-icon"/>
                                    </Typography.Link> 
                                    <Popconfirm
                                        title="Are you sure you want to delete this note?"
                                        onConfirm={() => { deleteNote(item.id) }}
                                        onCancel={cancel}
                                        okText="Yes"
                                        cancelText="No"
                                    >
                                        <DeleteOutlined className="action-icon"/>
                                    </Popconfirm>                        
                                </div>                            
                            </Card>
                            </Col>
                        ))
                    }
                </Row>
            :
                <>
                    <h2>You dont have any note yet</h2>
                </>
        }
  </>
    
  );
};

export default Notes;