import React, { useState } from "react";
import {
  Form,
  Input,
  Button,
  Typography,
  Row,
  Col,
  Space,
  message,
  Modal,
} from "antd";
import { useHistory } from "react-router-dom";
import user from "./../../services/user";
import "./signin.less";

const { Title, Text } = Typography;
const Signin = (props) => {
  const [isDisabled, setIsDisabled] = useState(false);
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [newUser, setNewUser] = useState(null);
  const [state, setState] = useState(null)
  const history = useHistory();

  const handleInput = (event) => {
    setState({
      ...state,
      [event.target.name]: event.target.value,
    });
  };

  const handleRegisterInput = (event) => {
    setNewUser({
      ...newUser,
      [event.target.name]: event.target.value,
    });
  };

  async function handleRegister() {
    try {
      let resp = await user.create(newUser);
      resp.status && message.success("Registered succesfully. Please Sign in");
      closeModal();
    } catch (error) {
      message.error("An Error has ocurred. Please try again");
    }
    
  }

  async function handleSignin() {
    try {
      let resp = await user.login(state);
      if (resp.status){
        setIsDisabled(true);
        localStorage.setItem("token", resp.data.token);
        localStorage.setItem("welcMessage", resp.data.message);
        history.push("/activeNotes");
        
      }
    } catch (error) {
      message.error("An Error has ocurred. Please try again");
    }
    
  };

  /** Functions for register modal */

  const showModal = () => {
    setState(null);
    setIsModalVisible(true);
    
  }

  const closeModal = () => {
    setIsModalVisible(false);
  }
  

  return (
    <>    
      <Row gutter={[0, { xs: 8, sm: 16, md: 24, lg: 32 }]}>
        <Col
          xs={{ span: 20, offset: 2 }}
          sm={{ span: 16, offset: 4 }}
          md={{ span: 14, offset: 5 }}
          lg={{ span: 6, offset: 9 }}
          xl={{ span: 6, offset: 9 }}
        >
          <Row justify="center" align="middle">
            <Col span={24}>
              <Space direction="vertical" className="container-login">
                <Title
                  level={4}
                  style={{
                    textAlign: "center",
                    fontWeight: "bold",
                    marginTop: 20,
                  }}
                >
                  Nolatech Challenge
                </Title>
                <br />
                <Form
                  name="normal_login"
                  onFinish={handleSignin}
                >
                  <Form.Item
                    name="username"
                    rules={[
                      {
                        required: true,
                        message: "Username can't be blank",
                      },
                      {
                        type: "email",
                        message: "Invalid E-mail",
                      },
                    ]}
                  >
                    <Input
                      size="large"
                      name="email"
                      disabled={isDisabled}
                      onChange={handleInput}
                      placeholder="Username"
                    />
                  </Form.Item>
                  <Form.Item
                    name="passwrd"
                    rules={[
                      {
                        required: true,
                        message: "Password can't be blank",
                      },
                    ]}
                  >
                    <Input.Password
                      size="large"
                      name="password"
                      disabled={isDisabled}
                      placeholder="Password"
                      onChange={handleInput}
                    />
                  </Form.Item>
                  <Form.Item>
                    <Button
                      size="large"
                      type="primary"
                      htmlType="submit"
                      disabled={isDisabled}
                      className="login-form-button"
                      style={{ marginRight: "15px" }}
                    >
                      Sign In
                    </Button>
                    <Button
                      size="large"
                      type="secondary"
                      className="login-form-button"
                      onClick={showModal}
                      style={{ marginRight: "15px" }}
                    >
                      Register
                    </Button>
                  </Form.Item>
                </Form>
              </Space>
            </Col>
          </Row>
        </Col>
      </Row>

      <Modal
        footer={null}
        className="container-modal"
        maskStyle={{ backgroundColor: "rgba(0,0, 0, 0.90)" }}
        visible={isModalVisible}
        onCancel={() => closeModal()}
        centered={true}
      >
        <Space direction="vertical" style={{ width: "100%" }}>
          <Row
            gutter={[8, { xs: 8, sm: 16, md: 24, lg: 32 }]}
            justify="center"
          >
            <Space direction="vertical" style={{}}>                    
                <Title level={4} style={{ color: "#00001E", textAlign: "center" }}>
                    Register
                </Title>
                <Text
                style={{
                    color: "#00001E",
                    fontSize: 16,
                    padding: "0px 24px",
                }}
                >
                Please enter your information
                </Text>
            </Space>
          </Row>
          <Row
            gutter={[8, { xs: 8, sm: 16, md: 24, lg: 32 }]}
            justify="center"
          >
            <Space direction="vertical">
              <Space direction="horizontal" size="small" style={{ width: "100%" }}>
                <Form onFinish={ handleRegister } >                      
                  <Form.Item
                    name="firstName"
                    rules={[
                      {
                        required: true,
                        message: "First Name can't be blank",
                      }
                    ]}
                  >
                    <Input
                      size="medium"
                      name="firstName"
                      disabled={isDisabled}
                      onChange={handleRegisterInput}
                      placeholder="First Name"
                    />
                  </Form.Item>
                  <Form.Item
                    name="lastName"
                    rules={[
                      {
                        required: true,
                        message: "Last Name can't be blank",
                      }
                    ]}
                  >
                    <Input
                      size="medium"
                      name="lastName"
                      disabled={isDisabled}
                      onChange={handleRegisterInput}
                      placeholder="Last Name"
                    />
                  </Form.Item>
                  <Form.Item
                    name="useremail"
                    rules={[
                      {
                        required: true,
                        message: "Email can't be blank",
                      },
                      {
                        type: "email",
                        message: "Invalid E-mail",
                      },
                    ]}
                  >
                    <Input
                      size="medium"
                      name="email"
                      disabled={isDisabled}
                      onChange={handleRegisterInput}
                      placeholder="Email"
                    />
                  </Form.Item>
                  <Form.Item
                    name="passwrd"
                    rules={[
                      {
                        required: true,
                        message: "Password can't be blank",
                      },
                    ]}
                  >
                    <Input.Password
                      size="medium"
                      name="password"
                      disabled={isDisabled}
                      placeholder="Password"
                      onChange={handleRegisterInput}
                    />
                  </Form.Item>
                  <Form.Item
                    name="passwrdConfirm"
                    rules={[
                      {
                        required: true,
                        message: "Confirm Password can't be blank",
                      },
                    ]}
                  >
                    <Input.Password
                      size="medium"
                      name="confirm"
                      disabled={isDisabled}
                      placeholder="Confirm Password"
                      onChange={handleRegisterInput}
                    />
                  </Form.Item>
                  <Row gutter={[32, { xs: 8, sm: 16, md: 24, lg: 32 }]} justify="end">
                    <Col
                      xs={{ span: 24, offset: 0 }}
                      sm={{ span: 24, offset: 0 }}
                      md={{ span: 12, offset: 0 }}
                      lg={{ span: 12, offset: 0 }}
                      xl={{ span: 12, offset: 0 }}
                    >
                      <div id="btn-previous" className="btn-previous-security">
                        <Button
                          block
                          size="medium"
                          className="btn-previous-security"
                          onClick={() => closeModal()}
                        >
                          Cancel
                        </Button>
                      </div>
                    </Col>
                    <Col
                      xs={{ span: 24, offset: 0 }}
                      sm={{ span: 24, offset: 0 }}
                      md={{ span: 12, offset: 0 }}
                      lg={{ span: 12, offset: 0 }}
                      xl={{ span: 12, offset: 0 }}
                    >
                      <Button
                        block
                        size="medium"
                        type="primary"
                        htmlType="submit"
                        style={{
                            height: "40px",
                            fontSize: "14px",
                            fontWeight: 600,
                            borderRadius: "5px",
                        }}
                      >
                        Register
                      </Button>
                    </Col>
                  </Row>
                </Form>
              </Space>
            </Space>
          </Row>
        </Space>
      </Modal>
    </>
    
  );
};

export default Signin;