import React, { useState } from "react";
import {
  Layout,
  Menu,
  message,  
} from "antd";
import "./layouts.less";
import {
  CheckCircleOutlined,
  MenuUnfoldOutlined,
  MenuFoldOutlined,
  PoweroffOutlined
} from "@ant-design/icons";
import Archive from "@2fd/ant-design-icons/lib/Archive";
import { NavLink } from "react-router-dom";

const { Header, Sider, Content } = Layout;

const DashboardLayout = (props) => {
  const [collapsedMenu, setCollapsedMenu] = useState(false);
  
  async function logoutUsr() {
    try {
      localStorage.removeItem("token");
      localStorage.removeItem("welcMessage");
      console.log(localStorage)
      message.success("Succesfully Logged Out")
    } catch (error) {
      console.log("error", error)
    }
  }

  return (
    <Layout style={{ minHeight: "100vh" }}>
      <Sider
        collapsible
        trigger={null}
        collapsed={collapsedMenu}
        className={collapsedMenu ? "sider-collapsed" : "sider-style"}
      >
        <div className="logo">
          
        </div>
        <Menu mode="inline" defaultSelectedKeys={["1"]}>
          <Menu.Item
            key="1"
            icon={<CheckCircleOutlined />}
          >
            <NavLink to="/activeNotes">Active Notes</NavLink>
          </Menu.Item>
          <Menu.Item
            key="2"
            icon={<Archive />}
          >
            <NavLink to="/archivedNotes">Archived Notes</NavLink>
          </Menu.Item>
        </Menu>
      </Sider>

      <Layout className="site-layout">
        <Header className="header-layout">
          {React.createElement(
            collapsedMenu ? MenuUnfoldOutlined : MenuFoldOutlined,
            {
              className: "trigger",
              onClick: () => setCollapsedMenu(!collapsedMenu),
            }
          )}
          <div
            style={{
              flexDirection: "row",
              display: "flex",
              alignItems: "center",
            }}
          >
            <h5>{localStorage.getItem("welcMessage")}</h5>
            <NavLink to="/signin"style={{ marginRight: "30px" }}>
              <div onClick={logoutUsr}>
                <PoweroffOutlined style={{marginRight: "4px"}} /> 
              </div>
            </NavLink>
          </div>
        </Header>
        <Content
          className="site-layout-background"
          style={{
            padding: 20,
            minHeight: 280,
          }}
        >
          {props.children}
        </Content>
      </Layout>
    </Layout>
  );
};

export default DashboardLayout;