import api from "./axios";

const user = {
  async login(body) {
    try {
      const resp = await api.post("users/login", body);
      return resp;
      
    } catch (error) {

      return error;
    }
  },

  async create(body){
    try {
      
      const { data } = await api.post("users/add", body);
      return data;

    } catch (error) {

      return error;      

    }
    
  }
};

export default user;