import api from "./axios";

const category = {
  async add(body) {
    try {
      const resp = await api.post("categories/add", body, {
        headers: {
          token: localStorage.getItem("token")
        },
      });
      return resp;
      
    } catch (error) {

      return error;
    }
  },

  async getAll(){
    try {      
      const { data } = await api.get("categories", {
        headers: {
          token: localStorage.getItem("token")
        },
      });
      return data;

    } catch (error) {

      return error;  
    }
    
  }
};

export default category;