import React, { useEffect, useState } from "react";
import { Typography, Row, Col, Button, Input, Form, Tag, Card, Tooltip, message, Select, Modal, Space } from "antd";
import { PlusOutlined } from "@ant-design/icons";
import { TweenOneGroup } from "rc-tween-one";
import { ArrowLeftOutlined, SaveOutlined, CloseCircleOutlined} from "@ant-design/icons";
import { useHistory, useParams } from "react-router-dom";
/** import category services */
import categoriesService from "./../../services/category";

/** notes service */
import noteService from "./../../services/note";

import "./notes.less";

const { Title } = Typography;
const { TextArea } = Input;
const { Option } = Select;

const EditNote = () => {
    const history = useHistory();

    const [form] = Form.useForm();

    const [note, setNote] = useState(null);

  /** Tags for categories */
  const [tags, setTags] = useState([]);
  const [categories, setCategories] = useState([]);
  /** to add new category */
  const [newCategory, setNewCategory] = useState(null);
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [create, setCreate] = useState(true);
  const [noteCategories, setNoteCategories] = useState([]);
  const { id } = useParams()


  useEffect(() => {
    getAllCategories();
    if(window.location.pathname !== "/edit"){
        setCreate(false);
        getNote(id);

    }else{
        setCreate(true);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])
  


  const handleClose = (removedTag) => {
    const newTags = tags.filter((tag) => tag !== removedTag);
    setTags(newTags);
  };

  const forMap = (tag) => {
    const tagElem = (
      <Tag
        closable
        onClose={(e) => {
          e.preventDefault();
          handleClose(tag);
          let aux = [...categories, { name: tag, value: tag}];
          // let stat = noteCategories.filter((item) => item !== tag);
          // setNoteCategories(stat);
          setCategories(aux);
        }}
      >
        {tag}
      </Tag>
    );
    return (
      <span
        key={tag}
        style={{
          display: "inline-block"
        }}
      >
        {tagElem}
      </span>
    );
  };

  const tagChild = tags.map(forMap);


  const onFinish = () => {
    console.log("Edited", note);
  };

  const handleInput = (e) => {
    setNote({
      ...note,
      [e.target.name]: e.target.value
    });
  };

  const handleCatInput = (e) => {
    setNewCategory({ name: e.target.value });
  }

  /** update note */
  async function updateNote(id){
    setNote({
        ...note,
        categories: noteCategories
    })
    try {
        let resp = await noteService.update(id, note);
        if(resp?.status){
            message.success("Updated successfully");
        }
    } catch (error) {
        
    }
  }

  async function getNote(id){
    try {
        let resp = await noteService.getByID(id);
        setNote(resp.data[0].Notes[0]);
        
        form.setFieldsValue({
            title: resp.data[0].Notes[0].title,
            content: resp.data[0].Notes[0].content
        })       
    } catch (error) {
        
    }
  }

  /** create a new note */

  async function createNote(){
    setNote({
        ...note,
        categories: noteCategories
    })
    try {
        let resp = await noteService.create(note);
        resp && message.success("Updated succesfully");
        
    } catch (error) {
        message.error("Please try again");
        
    }
  }


  /** Register a new category */
  async function addNewCategory () {
    try {
        let resp = await categoriesService.add(newCategory);
        if (resp.status){
            message.success("Category registered");
    
            let aux = [...categories, { value: newCategory.name, name: newCategory.name }];
            setCategories(aux);
            closeModal();
        }    
    } catch (error) {
        message.success("An error has ocurred. Please try again");
    }    
  }

  const showModal = () => {
    setIsModalVisible(true);
  }

  const closeModal = () => {
    setNewCategory(null);   
    setIsModalVisible(false);
    document.getElementById("description").value = "";
  }


  /** Get All Categories */
  async function getAllCategories (){
    try {
        let resp = await categoriesService.getAll();
        if (resp.status){
            let aux = resp.data;
            setCategories(aux);
        }    
    } catch (error) {
        message.error("An error has ocurred. Please try again");
    }        
  }

  return (
    <>
        <Card className="card-dashboard">            
            <Row> 
                <Col span={6} style={{ display: "flex", flexDirection: "row", flex: "0 0 10%" }}> 
                    <Typography.Link onClick={() => history.push("/activeNotes")}> 
                        <ArrowLeftOutlined style={{ fontSize: "20px" }} /> 
                    </Typography.Link> 
                </Col> 
                <Col span={6}> 
                    <h1 style={{ marginTop: "-3px", marginLeft: "-10px" }}> Edit Note</h1> 
                </Col> 
            </Row> 
            <Row> 
                <Col span={10}>
                    <Card className="tags-card" bodyStyle={{ padding: "0"}}>
                        <div style={{ marginBottom: 16, padding: "10px" }} > 
                            <TweenOneGroup 
                                enter={{ scale: 0.8, opacity: 0, type: "from", duration: 100 }} 
                                onEnd={(e) => { 
                                    if (e.type === "appear" || e.type === "enter") { 
                                        e.target.style = "display: inline-block"; 
                                    } }} 
                                leave={{ opacity: 0, width: 0, scale: 0, duration: 200 }} 
                                appear={false} > 
                                    {tagChild} 
                            </TweenOneGroup> 
                        </div>
                    </Card>
                </Col> 
                <Col span={8}>
                    <Select 
                        style={{ width: "30%" }} 
                        size="medium" 
                        placeholder="Categories"
                        className="cat-select"
                        onChange={(value, key) => {
                            let stat = categories.filter((item) => item.value !== value);
                            let catAux= [...noteCategories, key]
                            setNoteCategories(catAux);             
                            setCategories(stat);
                            setTags([...tags, value]);
                        }}
                    >
                        {categories.map((item, i) => {
                        return <Option key={item.id} value={item.name}>{item.name}</Option>;
                        })}
                    </Select>
                </Col>
                <Col span={6}>
                    <Button type="primary" size="medium" style={{ margin: "auto" }} onClick={showModal}>
                        <PlusOutlined /> Add Category
                    </Button>
                </Col>
            </Row> 
            <Row justify="center" align="middle" style={{ padding: "30px" }}> 
                <Col span={24}> 
                    <Form form={form} onFinish={onFinish}> 
                        <Form.Item style={{ marginLeft: "auto" }} name="button"> 
                            <Tooltip placement="topLeft" title="Cancel">                            
                                <Button size="large" type="secondary" className="form-btn" onClick={() => history.push("/activeNotes")} style={{ marginRight: "15px", float: "right" }} > 
                                    <CloseCircleOutlined /> 
                                </Button>                             
                            </Tooltip>
                            <Tooltip placement="topLeft" title="Save">
                                <Button size="large" type="primary" htmlType="submit" className="form-btn" 
                                    onClick={() => {
                                        window.location.pathname === "/edit" ?
                                            createNote()
                                        :
                                            updateNote(id)
                                    }} 
                                    style={{ marginRight: "15px", float: "right" }} > 
                                    <SaveOutlined /> 
                                </Button> 
                            </Tooltip>
                        </Form.Item> 
                        <Form.Item name="title" rules={[ { required: true, message: "Title can't be blank" } ]} > 
                            <Input size="large" name="title" onChange={handleInput} placeholder="Title" /> 
                        </Form.Item> 
                        <Form.Item name="content"> 
                            <TextArea rows={20} placeholder="Content" rules={[ { required: true, message: "Content can't be blank" } ]} onChange={handleInput} name="content" /> 
                        </Form.Item> 
                    </Form> 
                </Col> 
            </Row>
        </Card>
        
        <Modal
          footer={null}
          className="container-modal"
          maskStyle={{ backgroundColor: "rgba(0,0, 0, 0.90)" }}
          visible={isModalVisible}
          onCancel={() => closeModal()}
          centered={true}
        >
            <Space direction="vertical" style={{ width: "100%" }}>
                <Row
                    gutter={[8, { xs: 8, sm: 16, md: 24, lg: 32 }]}
                    justify="center"
                >
                    <Space direction="vertical" style={{}}>                    
                        <Title level={4} style={{ color: "#00001E", textAlign: "center" }}>
                            Add new Category
                        </Title>
                    </Space>
                </Row>
                <Row
                    gutter={[8, { xs: 8, sm: 16, md: 24, lg: 32 }]}
                    justify="center"
                >
                    <Space direction="vertical">
                        <Space direction="horizontal" size="small" style={{ width: "100%" }}>
                            <Form onFinish={ addNewCategory } >                      
                                <Form.Item
                                    name="description"
                                    rules={[
                                        {
                                        required: true,
                                        message: "Category description can't be blank",
                                        }
                                    ]}
                                >
                                    <Input
                                        size="medium"
                                        name="description"
                                        id="description"
                                        onChange={ handleCatInput }
                                        placeholder="Category description"
                                    />
                                </Form.Item>
                                <Row gutter={[32, { xs: 8, sm: 16, md: 24, lg: 32 }]} justify="end">
                                    <Col
                                        xs={{ span: 24, offset: 0 }}
                                        sm={{ span: 24, offset: 0 }}
                                        md={{ span: 12, offset: 0 }}
                                        lg={{ span: 12, offset: 0 }}
                                        xl={{ span: 12, offset: 0 }}
                                    >
                                        <div id="btn-previous" className="btn-previous-security">
                                            <Button
                                                block
                                                size="medium"
                                                className="btn-previous-security"
                                                onClick={() => closeModal()}
                                            >
                                                Cancel
                                            </Button>
                                        </div>
                                    </Col>
                                    <Col
                                        xs={{ span: 24, offset: 0 }}
                                        sm={{ span: 24, offset: 0 }}
                                        md={{ span: 12, offset: 0 }}
                                        lg={{ span: 12, offset: 0 }}
                                        xl={{ span: 12, offset: 0 }}
                                    >
                                        <Button
                                            block
                                            size="medium"
                                            type="primary"
                                            htmlType="submit"
                                            style={{
                                                height: "40px",
                                                fontSize: "14px",
                                                fontWeight: 600,
                                                borderRadius: "5px",
                                            }}
                                        >
                                            Register
                                        </Button>
                                    </Col>
                                </Row>
                            </Form>
                        </Space>
                    </Space>
                </Row>
            </Space>
        </Modal>

                    
    </>
  )
}

export default EditNote