import api from "./axios";
const token = localStorage.getItem("token");

const user = {
  async getActive() {
    try {
      const resp = await api.get("notes/",{
        headers: {
          token: token
        },
      });
      return resp;
      
    } catch (error) {

      return error;
    }
  },

  async getArchived(){
    try {      
      const { data } = await api.get("notes/archived",{
        headers: {
          token: token
        },
      });
      return data;

    } catch (error) {
        
      return error;  
    }
    
  },
  async getByID(id){
    try {      
      const { data } = await api.get("notes/" + id,{
        headers: {
          token: token
        },
      });
      return data;

    } catch (error) {
        
      return error;  
    }
    
  },
  async create(body){
    try {      
      const { data } = await api.post("notes/add", body, {
        headers: {
          token: token
        },
      });
      return data;

    } catch (error) {
        
      return error;  
    }    
  },
  async update(id, body){
    try {      
      const { data } = await api.put("notes/" + id, body, {
        headers: {
          token: token
        },
      });
      return data;

    } catch (error) {
        
      return error;  
    }    
  },  
  async delete(id){
    try {      
      const { data } = await api.put("notes/delete/" + id, {}, {
        headers: {
          token: token
        },
      });
      return data;

    } catch (error) {
        
      return error;  
    }    
  },
  async archive(id){
    try {      
      const { data } = await api.put("notes/store/" + id, {}, {
        headers: {
          token: token
        },
      });
      return data;

    } catch (error) {
        
      return error;  
    }    
  },
  async activate(id, tokenParams){
    try {
      const { data } = await api.put("notes/activate/" + id, {},{
        headers: {
            token: tokenParams
          },
      });
      return data;

    } catch (error) {
      return error;  
    }    
  }
};

export default user;